<template>
  <div class="container-fluid">
    <main role="main" class="mt-5">
      <h3 class="text-center"></h3>
      <table class="table">
        <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th style="text-align: right"></th>
        <th style="text-align: right"></th>
        </thead>
        <tbody>
        <tr v-for="item in cart.carts" :key="item.id">
          <td class="align-middle">
            <button type="button" class="btn btn-outline-danger btn-sm"
                    @click="deleteCartItem(item.product ,item.id)">
              <i class="far fa-trash-alt"></i>
            </button>
          </td>
          <td class="align-middle">
            {{ item.product.title }}
            <div class="text-success" v-if="item.coupon">
              
            </div>
          </td>
          <td class="align-middle">{{ item.qty }}  {{ item.product.unit }}</td>
          <td class="align-middle">{{ item.product.price | currency }}</td>
          <td class="align-middle text-right">{{ item.total - item.final_total | currency }}</td>
          <td class="align-middle text-right">{{ item.final_total | currency }}</td>
        </tr>
        </tbody>
        <tfoot>
        <tr>
          <td colspan="5" class="text-right"></td>
          <td class="text-right">{{ cart.final_total | currency }}</td>
        </tr>
        <tr v-if=" cart.total !== cart.total ">
          <td colspan="5" class="text-right text-success"></td>
          <td class="text-right text-success">{{ cart.final_total | currency }}</td>
        </tr>
        </tfoot>
      </table>
      <div class="form-group">
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" placeholder=""
                 name="coupon"
                 v-model="coupon_code">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button"
                    @click="addCouponCode">
              
            </button>
          </div>
        </div>
        <span class="text-danger" v-if=" getCouponActiv "></span>
      </div>
      <div class="my-5 row justify-content-center">
        <div class="col-md-12 row justify-content-center">
          <h3></h3>
          <div class="col-md-12 row  justify-content-center">
            <form class="col-md-6" @submit.prevent="creatOrder">
              <div class="form-group">
                <label for="useremail">Email</label>
                <input type="email" class="form-control" name="email" id="useremail"
                       placeholder=" Email"
                       v-model="form.user.email"
                       :class="{ 'is-invalid' : errors.has('email') }"
                       v-validate="'required|email'">
                <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
              </div>
              <div class="form-group">
                <label for="username"></label>
                <input type="text" class="form-control" name="name" id="username"
                       v-model="form.user.name"
                       v-validate="'required'"
                       :class="{ 'is-invalid' : errors.has('name') }"
                       placeholder="">
                <span class="text-danger" v-if="errors.has('name')"></span>
              </div>
              <div class="form-group">
                <label for="usertel"></label>
                <input type="tel" class="form-control" name="tel" id="usertel"
                       placeholder=""
                       v-model="form.user.tel"
                       v-validate="'required|digits:10'"
                       :class="{ 'is-invalid' : errors.has('tel') }">
                <span class="text-danger" v-if="errors.has('tel')"></span>
              </div>
              <div class="form-group">
                <label for="useraddress"></label>
                <input type="text" class="form-control" name="address" id="useraddress"
                       v-model="form.user.address"
                       placeholder=""
                       v-validate="'required'"
                       :class="{ 'is-invalid' : errors.has('address') }">
                <span class="text-danger" v-if="errors.has('address')"></span>
              </div>
              <div class="form-group">
                <label for="comment"></label>
                <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div class="footer">
        <div class="container-fluid text-center">
          <router-link to="/product">
            <button class="btn btn-secondary" style="margin-right: 10px"></button>
          </router-link>
          <button class="btn btn-primary" @click="creatTransList"></button>
        </div>
      </div>
    </main>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'cart',
  data () {
    return {
      coupon_code: '',
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      }
    }
  },
  computed: {
    ...mapGetters('CartModules', ['cart']),
    getCouponActiv () {
      return this.$store.state.showCoupon_error
    }
  },
  methods: {
    ...mapActions('CartModules', ['getCart']),
    deleteCartItem (item, id) {
      this.$store.dispatch('CartModules/removeCart', id)
    },
    addCouponCode () {
      const vm = this
      this.$store.dispatch('addCouponCode', vm.coupon_code)
    },
    creatTransList () {
      const vm = this
      this.$validator.validate().then(result => {
        if (result) {
          this.$store.dispatch('creatTransList', vm.form)
          vm.$router.push(`/product/payment`)
        } else {
          console.log('')
        }
      })
    }
  }
}
</script>

