export default {
  namespaced: true,
  state: {
    isLoading: false
  },
  actions: { // 
    updateLoading (context, status) {
      context.commit('LoadingModules/LOADING', status, { root: true }) // commitmutations function
    }
  },
  mutations: { // 
    LOADING (state, payload) {
      state.isLoading = payload
    }
  },
  getters: {
    Loading (state) {
      return state.isLoading
    }
  },
  modules: {
    // vuex
  }
}
