import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'
import 'bootstrap'
import Loading from 'vue-loading-overlay' // 
import 'vue-loading-overlay/dist/vue-loading.css'
import VeeValidate from 'vee-validate' // 
import TW from 'vee-validate/dist/locale/zh_TW' // 
import './assets/scss/main.scss'
import VueAnimate from 'vue-animate-scroll'

import App from './App.vue'
import router from './router'
import currencyFilter from '@/filters/currency'
import correctTime from '@/filters/date'
import store from './store'
// 
import VueScrollReveal from 'vue-scroll-reveal'
// searchPlugin,css
import Autocomplete from '@trevoreyre/autocomplete-vue'
Vue.config.productionTip = false

Vue.use(VueAxios, axios) // axiosbindvue,  this.axios.get()this.$http.get()ajax
Vue.use(Vuex)
Vue.use(VeeValidate)
VeeValidate.Validator.localize('zh-TW', TW) // 
// cookies
axios.defaults.withCredentials = true
// loading
Vue.component('Loading', Loading)
Vue.filter('currency', currencyFilter)
Vue.filter('dateTime', correctTime)
Vue.use(VueScrollReveal, {
  class: 'v-scroll-reveal',
  duration: 3000,
  delay: 200,
  scale: 1,
  distance: '10px',
  mobile: false
})
Vue.use(Autocomplete)
Vue.use(VueAnimate)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
// 
router.beforeEach((to, from, next) => {
  // console.log(to, from, next)
  if (to.meta.requiresAuth) {
    const api = `${process.env.VUE_APP_API_PATH}/api/user/check`
    // const vm = this
    axios.post(api).then((response) => {
      // console.log('response', response)
      if (response.data.success) {
        next()
      } else {
        next({
          path: 'login'
        })
      }
    })
  } else {
    next()
  }
})
