<template>
  <div class="table-responsive">
    <div class="text-right">
      <button class="btn btn-outline-primary mt-4" @click="openModal('new')"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <td width="80"></td>
          <td width="120"></td>
          <td></td>
          <td class="text-center" width="120"></td>
          <td class="text-center" width="120"></td>
          <td width="80"></td>
          <td class="text-center" width="160"></td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id" >
          <td>{{ item.num }}</td>
          <td>{{ item.category}}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.origin_price | currency }}</td>
          <td class="text-right">{{ item.price | currency }}</td>
          <td class="text-center">
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td class="text-center">
            <button class="btn btn-outline-primary btn-sm" @click="openModal('edit', item)"></button><span>&nbsp;</span>
            <button class="btn btn-outline-danger btn-sm" @click="openModal('delete', item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
<!--    -->
    <Pagination :page-data="pagination" @updatePage="getPage"
    ></Pagination>
<!--    -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
         aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" >
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image"
                         placeholder="" v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile"> 
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control"
                         ref="files" @change="uploadFile">
                </div>
                <img :src="tempProduct.imageUrl"
                  class="img-fluid" alt="">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title"
                         placeholder="" v-model.trim="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category"
                           placeholder="" v-model.trim="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit"
                           placeholder="" v-model.trim="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price"
                           placeholder="" v-model.trim="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price"
                           placeholder="" v-model.trim="tempProduct.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description"
                            placeholder="" v-model.trim="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content"
                            placeholder="" v-model.trim="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox"
                           id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary"
                    @click="updateProduct"
            ></button>
          </div>
        </div>
      </div>
    </div>
<!--    -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
         aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger"
                    @click="updateProduct"
            ></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
import $ from 'jquery'

export default {
  name: 'products',
  data () {
    return {
      products: [],
      page: {},
      pagination: {},
      tempProduct: {
        origin_price: {
          Type: Number,
          required: true
        },
        price: {
          Type: Number,
          required: true
        }
      },
      modalType: '',
      status: {
        fileUploading: false
      }
    }
  },
  components: {
    Pagination
  },
  methods: {
    getProducts (page = 1) {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/products?page=${page}`
      const vm = this
      // vm.$store.state.isLoading = true
      // dispatch  vuexaction function
      vm.$store.dispatch('updateLoading', true)
      // this.$http.getthis.axios.getaxiosbindvuevue-axios
      this.$http.get(api).then((response) => {
        // console.log(response)
        vm.$store.dispatch('updateLoading', false)
        vm.products = response.data.products
        vm.pagination = response.data.pagination
      })
    },
    //  modal(popup)  ( modal)
    openModal (type, item) {
      if (type === 'delete') {
        this.tempProduct = Object.assign({}, item) // es6itemtempProduct
        this.modalType = type
        $('#delProductModal').modal('show')
      } else {
        this.tempProduct = Object.assign({}, item) // es6itemtempProduct
        if (type === 'new') {
          this.tempProduct = {}
        }
        this.modalType = type
        $('#productModal').modal('show')
      }
    },
    updateProduct () {
      let api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/product`
      let httpMethod
      const vm = this
      if (vm.modalType === 'edit') {
        api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
      } else if (vm.modalType === 'delete') {
        api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'delete'
      } else if (vm.modalType === 'new') {
        httpMethod = 'post'
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {
        // console.log(response)
        if (response.data.success) {
          $('#productModal').modal('hide')
          $('#delProductModal').modal('hide')
          vm.getProducts(vm.page)
          console.log('')
        } else {
          $('#productModal').modal('hide')
          $('#delProductModal').modal('hide')
          vm.getProducts(vm.page)
          console.log('')
        }
        vm.modalType = ''
      })
    },
    uploadFile () {
      console.log('file', this)
      const uploadedFile = this.$refs.files.files[0]
      const vm = this
      // formdata
      const formData = new FormData() // https://developer.mozilla.org/zh-CN/docs/Web/API/FormData/FormData
      formData.append('file-to-upload', uploadedFile) // key
      const url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/upload`
      vm.status.fileUploading = true
      this.$http.post(url, formData, {
        headers: {
          'Content-type': 'multipart/form-data'
        }
      }).then((response) => {
        console.log('', response.data)
        vm.status.fileUploading = false
        if (response.data.success) {
          // vm.tempProduct.imageUrl = response.data.imageUrl
          // vm.$set
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
        } else {
          // eventbus alert
          //  'messsage:push'
          // message: 
          // status:  warning
          this.$bus.$emit('message:push', response.data.message, 'danger')
        }
      })
    },
    // 
    getPage (page) {
      this.page = page
      this.getProducts(page)
    }
  },
  created () {
    this.getProducts()
    //  eventbus
    //  'messsage:push'
    // message: 
    // status:  warning
    // this.$bus.$emit('message:push', '', 'success')
  }
}
</script>

