
import Axios from 'axios'
export default {
  strict: true, // vuex
  // state
  // actions,mutations,getters
  namespaced: true, // true actions,mutations,getters
  state: {
    cart: {},
    cartNum: '',
    cartId: '',
    alert: false
  },
  actions: { // 
    // contextvuexstatus(payload)
    //  {
    //   state,      //  `store.state`
    //     rootState,  //  `store.state`
    //     commit,     //  `store.commit`
    //     dispatch,   //  `store.dispatch`
    //     getters,    //  `store.getters`
    //     rootGetters //  `store.getters`
    //  }
    // actionmutationsajax
    getCart (context) {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/cart`
      Axios.get(api).then((response) => {
        context.commit('GETCART', response.data.data)
      })
    },
    addCart (context, { id, qty }) {
      const url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/cart`
      const cart = {
        'product_id': id,
        qty
        // es6'qty':qyt
      }
      context.commit('LoadingModules/LOADING', true, { root: true })
      Axios.post(url, { data: cart }).then(response => {
        context.commit('LoadingModules/LOADING', false, { root: true }) // { root: true } 
        // { root: true } storeLOADINGmodulesModules
        context.commit('ADDCART', response.data.id)
        context.dispatch('getCart')
      })
    },
    removeCart (context, id) {
      const url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/cart/${id}`
      context.commit('LoadingModules/LOADING', true, { root: true })
      Axios.delete(url).then(response => {
        if (response.data.success) {
          context.commit('LoadingModules/LOADING', false, { root: true })
          context.dispatch('getCart')
        } else {
          console.log('', response.data.message)
        }
      })
    },
    alertMessage (context, val) {
      context.commit('ALERTACTIVE', val)
    }
  },
  mutations: { // 
    // vuex
    // state state
    //  payload
    // mutationdebugajax
    GETCART (state, payload) {
      state.cart = payload
      // console.log('payload', payload)
      state.cartNum = payload.carts.length
    },
    ADDCART (state, payload) {
      state.cartId = payload
    },
    ALERTACTIVE (state, payload) {
      state.alert = payload
    }
  },
  getters: { // computed
    cart (state) {
      return state.cart
    },
    getAlert (state) {
      return state.alert
    }
  },
  modules: {
    // vuex
  }
}
