<template>
  <div class="table-responsive">
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-outline-primary mt-4" @click="openModal('new')"></button>
    </div>
    <table class="table mt-4">
      <thead>
      <tr>
        <td width="80"></td>
        <td></td>
        <td width="80"></td>
        <td width="120"></td>
        <td width="80"></td>
        <td class="text-center" width="160"></td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item) in coupons" :key="item.id" >
        <td>{{ item.num }}</td>
        <td>{{ item.title }}</td>
        <td>{{ item.percent }}</td>
        <td>{{ item.due_date }}</td>
        <td class="text-center">
          <span v-if="item.is_enabled" class="text-success"></span>
          <span v-else></span>
        </td>
        <td class="text-center">
          <button class="btn btn-outline-primary btn-sm" @click="openModal('edit', item)"></button><span>&nbsp;</span>
          <button class="btn btn-outline-danger btn-sm" @click="openModal('delete', item)"></button>
        </td>
      </tr>
      </tbody>
    </table>
    <!--    -->
    <Pagination :page-data="pagination" @updatePage="getPage"
    ></Pagination>
    <!--    -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
         aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" >
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title"
                         placeholder="" v-model="tempCoupon.title">
                </div>
                <div class="form-group">
                  <label for="percent"></label>
                  <input type="number" class="form-control" id="percent"
                         placeholder=""
                         v-model.number="tempCoupon.percent"
                         min="0" max="100">
                </div>
                <div class="form-group">
                  <label for="due-date"></label>
                  <input type="date" class="form-control" id="due-date"
                         placeholder="" v-model="tempCoupon.due_date">
                </div>
                <div class="form-group">
                  <label for="code"> </label>
                  <input type="text" class="form-control" id="code"
                         placeholder="" v-model="tempCoupon.code">
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox"
                           id="is_enabled" v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary"
                    @click="updateCoupon"
            ></button>
          </div>
        </div>
      </div>
    </div>
    <!--    -->
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
         aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempCoupon.title }}</strong>  ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger"
                    @click="updateCoupon"
            ></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Pagination from '@/components/Pagination'

export default {
  name: 'coupon',
  components: {
    Pagination
  },
  data () {
    return {
      coupons: [],
      page: {},
      pagination: {},
      tempCoupon: {
        percent: {
          Type: Number,
          required: true
        }
      },
      modalType: '',
      isLoading: false
    }
  },
  methods: {
    openModal (type, item) {
      if (type === 'delete') {
        this.tempCoupon = Object.assign({}, item)
        this.modalType = type
        $('#delCouponModal').modal('show')
      } else {
        this.tempCoupon = Object.assign({}, item) // es6itemtempCoupon
        if (type === 'new') {
          this.tempCoupon = {}
        }
        this.modalType = type
        $('#couponModal').modal('show')
      }
    },
    getCoupon (page = 1) {
      const url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupons?page=${page}`
      const vm = this
      vm.isLoading = true
      this.$http.get(url).then((response) => {
        console.log(response)
        vm.isLoading = false
        vm.coupons = response.data.coupons
        vm.pagination = response.data.pagination
      })
    },
    updateCoupon (code) {
      let url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon`
      let httpMethod
      const vm = this
      if (vm.modalType === 'edit') {
        url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon/${vm.tempCoupon.id}`
        httpMethod = 'put'
      } else if (vm.modalType === 'delete') {
        url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOM_PATH}/admin/coupon/${vm.tempCoupon.id}`
        httpMethod = 'delete'
      } else if (vm.modalType === 'new') {
        httpMethod = 'post'
      }
      this.$http[httpMethod](url, { data: vm.tempCoupon }).then((response) => {
        // console.log(response)
        if (response.data.success) {
          $('#couponModal').modal('hide')
          $('#delCouponModal').modal('hide')
          vm.getCoupon(vm.page)
          console.log('')
        } else {
          $('#couponModal').modal('hide')
          $('#delCouponModal').modal('hide')
          vm.getProducts(vm.page)
          console.log('')
        }
        vm.modalType = ''
      })
    },
    // 
    getPage (page) {
      this.page = page
      this.getCoupon(page)
    }
  },
  created () {
    this.getCoupon()
  }
}
</script>

